<template>
  <div class="vital">

    <div class="columns">
      <div class="column is-10 is-offset-1">
        <div class="box">
          <div>
            <h1>
              Vital
              <a class="button is-primary is-medium is-pulled-right" @click="showAddModal()">
                <span class="icon is-medium">
                  <i class="fa fa-plus"></i>
                </span>
                <span>Add New Vital</span>
              </a>
            </h1>
          </div>

          <!-- Show Scheduled Vital -->
          <table v-if="vitals.length" class="table is-hoverable is-fullwidth is-striped">
            <thead>
              <tr>
                <th>{{ $t('referenceLabel') }}</th>
                <th>Blood Pressure</th>
                <th>Oximetry</th>
                <th>Pulse</th>
                <th>Respiration</th>
                <th>Temperature</th>
                <th>Blood Glucose</th>
                <th>{{ $t('promptTimeLabel') }}</th>
                <th>{{ $t('startDateLabel') }}</th>
                <th>{{ $t('endDateLabel') }}</th>
                <th>{{ $t('repeatLabel') }}</th>
                <th>{{ $t('missedAlertLabel') }}</th>
                <th>{{ $t('actionsLabel') }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(showvital, index) in vitals" :key="showvital.id">
                <td>{{ showvital.reference }} </td>
                <td>
                  <b-tooltip
                    v-if="showvital.use_systolic == true"
                    :label="showvital.systolic_min + ' - ' + showvital.systolic_max"
                    position="is-right">
                      <b-icon icon="check" type="is-info"></b-icon>
                  </b-tooltip>
                  <b-icon v-if="showvital.use_systolic == false" icon="times" type="is-dark"></b-icon>
                </td>
                <td>
                  <b-tooltip
                    v-if="showvital.use_oximetry == true"
                    :label="showvital.oximetry_min + ' - ' + showvital.oximetry_max"
                    position="is-right">
                      <b-icon icon="check" type="is-info"></b-icon>
                  </b-tooltip>
                  <b-icon v-if="showvital.use_oximetry == false" icon="times" type="is-dark"></b-icon>
                </td>
                <td>
                  <b-tooltip
                    v-if="showvital.use_pulse == true"
                    :label="showvital.pulse_min + ' - ' + showvital.pulse_max"
                    position="is-right">
                      <b-icon icon="check" type="is-info"></b-icon>
                  </b-tooltip>
                  <b-icon v-if="showvital.use_pulse == false" icon="times" type="is-dark"></b-icon>
                </td>
                <td>
                  <b-tooltip
                    v-if="showvital.use_respiration == true"
                    :label="showvital.respiration_min + ' - ' + showvital.respiration_max"
                    position="is-right">
                      <b-icon icon="check" type="is-info"></b-icon>
                  </b-tooltip>
                  <b-icon v-if="showvital.use_respiration == false" icon="times" type="is-dark"></b-icon>
                </td>
                <td>
                  <b-tooltip
                    v-if="showvital.use_temperature == true"
                    :label="showvital.temperature_min + ' - ' + showvital.temperature_max"
                    position="is-right">
                      <b-icon icon="check" type="is-info"></b-icon>
                  </b-tooltip>
                  <b-icon v-if="showvital.use_temperature == false" icon="times" type="is-dark"></b-icon>
                </td>
                <td>
                  <b-tooltip
                    v-if="showvital.use_blood_glucose == true"
                    :label="showvital.blodd_glucose_min + ' - ' + showvital.blood_glucose_max"
                    position="is-right">
                      <b-icon icon="check" type="is-info"></b-icon>
                  </b-tooltip>
                  <b-icon v-if="showvital.use_blood_glucose == false" icon="times" type="is-dark"></b-icon>
                </td>
                <td>{{ showvital.prompt_time }} </td>
                <td>{{ showvital.start_date }}</td>
                <td>{{ showvital.end_date }} </td>
                <td>{{ showvital.repeat }}</td>
                <td>{{ showvital.alert_time }}</td>

                <td>
                  <div class="buttons">
                  <b-button type="is-success" icon-left="edit" @click="showEditModal(index)" title="Edit" ></b-button>
                  <b-button type="is-info" icon-left="copy" @click="showCopyModal(index)" title="Duplicate"></b-button>
                  <b-button type="is-danger" icon-left="trash" @click="showDeleteModal(index)" title="Delete"></b-button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>

        </div>
      </div> <!-- end Current vital list -->

      <!-- Modal to Add Vital -->
      <b-modal v-model="isModalVisible" scroll="keep" :can-cancel="false">
        <div class="modal-card" style="width:auto">
          <header class="modal-card-head">
            <p v-if="isAddModal" class="modal-card-title">Schedule New Vital</p>
            <p v-if="isCopyModal" class="modal-card-title">Schedule Duplicate Vital</p>
            <p v-if="isEditModal" class="modal-card-title">Edit Vital</p>
            <p v-if="isDeleteModal" class="modal-card-title">Delete Vital</p>
            <button class="delete" aria-label="close" @click="closeModal()"></button>
          </header>
          <section v-if="isDeleteModal" class="modal-card-body">
            <p>Are you sure you want to delete this vital?</p>
          </section>
          <section v-if="(isAddModal || isEditModal || isCopyModal)" class="modal-card-body">
            <!-- show errors -->
            <div v-if="errors.length > 0" class="message is-danger">
              <div class="message-body">
                <ul>
                  <li v-for="error in errors" :key="error.id">{{ error }}</li>
                </ul>
              </div>
            </div>

            <!-- show form -->
            <field-reference v-model="vital" is-required />

            <field-start-date v-model="vital" is-required />

            <field-end-date v-model="vital" />

            <field-prompt-time v-model="vital" is-required />

            <field-repeat v-model="vital" is-required />

            <field-missed-alert v-model="vital" is-required />

            <vital-checkbox v-model="vital" />

          <b-field horizontal label="Blood Pressure">
             <b-checkbox
              name="use_systolic"
              id="use_systolic"
              v-model="vital.use_systolic">
              </b-checkbox>
          </b-field>

          <b-field horizontal label="Systolic Range">
            <b-slider
              v-model="systolic"
              :min="30"
              :max="250"
              :step="1"
              :tooltip="false"
              indicator >
            </b-slider>
          </b-field>

          <b-field horizontal label="Diastolic Range">
            <b-slider
              v-model="diastolic"
              :min="30"
              :max="250"
              :step="1"
              :tooltip="false"
              indicator >
            </b-slider>
          </b-field>

          </section>

          <footer class="modal-card-foot">
            <button v-if="isAddModal || isCopyModal" class="button is-success" @click="addVital">Add Vital</button>
            <button v-if="isEditModal" class="button is-success" @click="updateVital">Save Changes</button>
            <button v-if="isDeleteModal" class="button is-danger" @click="deleteVital">Delete Vital</button>
            <button class="button" @click="closeModal()">Cancel</button>
          </footer>
        </div>
       </b-modal><!-- end addVitalModal -->

    </div>
  </div>
</template>

<script>

import FieldReference from '@/components/Fields/FieldReference.vue'
import FieldStartDate from '@/components/Fields/FieldStartDate.vue'
import FieldEndDate from '@/components/Fields/FieldEndDate.vue'
import FieldPromptTime from '@/components/Fields/FieldPromptTime.vue'
import FieldRepeat from '@/components/Fields/FieldRepeat.vue'
import FieldMissedAlert from '@/components/Fields/FieldMissedAlert.vue'
import VitalCheckbox from '@/components/Fields/VitalCheckbox.vue'

export default {

  components: {
    FieldReference,
    FieldStartDate,
    FieldEndDate,
    FieldPromptTime,
    FieldRepeat,
    FieldMissedAlert,
    VitalCheckbox
  },

  data () {
    return {
      vitals: [],
      vital: {},
      errors: [],
      isModalVisible: false,
      isAddModal: false,
      isEditModal: false,
      isCopyModal: false,
      isDeleteModal: false,
      systolic: [],
      diastolic: []
    }
  },

  mounted () {
    this.getVitals()
  },

  methods: {

    closeModal () {
      this.vital.id = ''
      this.vital.reference = ''
      this.vital.start_date = null
      this.vital.end_date = null
      this.vital.prompt_time = null
      this.vital.repeat = ''
      this.vital.use_systolic = null
      this.vital.use_diastolic = null
      this.vital.use_oximetry = null
      this.vital.use_pulse = null
      this.vital.use_respiration = null
      this.vital.use_temperature = null
      this.vital.use_blood_glucose = null
      this.vital.alert_time = null
      this.isModalVisible = false
      this.isAddModal = false
      this.isEditModal = false
      this.isCopyModal = false
      this.isDeleteModal = false
    },

    showAddModal () {
      this.vital.start_date = new Date()
      this.vital.alert_time = 1
      this.isAddModal = true
      this.isModalVisible = true
    },

    showEditModal (index) {
      this.vital.id = this.vitals[index].id
      this.vital.reference = this.vitals[index].reference
      this.vital.repeat = this.vitals[index].repeat
      this.vital.alert_time = this.vitals[index].alert_time
      this.vital.start_date = new Date(this.vitals[index].start_date)
      this.vital.end_date = this.vitals[index].end_date ? new Date(this.vitals[index].end_date) : null
      this.vital.prompt_time = new Date('1970-01-01T' + this.vitals[index].prompt_time)
      this.isEditModal = true
      this.isModalVisible = true
    },

    showCopyModal (index) {
      this.vital.reference = this.vitals[index].reference + ' (copy)'
      this.vital.repeat = this.vitals[index].repeat
      this.vital.alert_time = this.vitals[index].alert_time
      this.vital.start_date = new Date(this.vitals[index].start_date)
      this.vital.end_date = this.vitals[index].end_date ? new Date(this.vitals[index].end_date) : null
      this.vital.prompt_time = new Date('1970-01-01T' + this.vitals[index].prompt_time)
      this.isCopyModal = true
      this.isModalVisible = true
    },

    showDeleteModal (index) {
      this.vital.id = this.vitals[index].id
      this.isDeleteModal = true
      this.isModalVisible = true
    },

    // Get Scheduled Vitals
    getVitals () {
      this.$http.get('vital_schedules')
        .then(response => {
          this.vitals = response.data
        })
    },

    addVital () {
      const myvital = this.vital
      myvital.prompt_time = myvital.prompt_time.toLocaleString()
      console.log(myvital.prompt_time)
      this.$http.post('vital_schedules', myvital)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getVitals()
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    updateVital () {
      const myvital = this.vital
      myvital.prompt_time = myvital.prompt_time.toLocaleString()
      this.$http.put('vital_schedules/' + myvital.id, myvital)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getVitals()
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    deleteVital () {
      const myvitalid = this.vital.id
      this.$http.delete('vital_schedules/' + myvitalid)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getVitals()
        })
        .catch(function (error) {
          console.log(error)
        })
    }

  } // end methods

}
</script>
