<template>
  <b-field horizontal label="Blood Pressure">
    <b-checkbox
      name="use_systolic"
      id="use_systolic"
      v-model="value.use_systolic">
    </b-checkbox>
  </b-field>
</template>

<script>
export default {
  props: {
    value: {
      type: Object,
      required: true
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  }

}
</script>
