<template>
  <div class="event">

    <div class="columns">
      <div class="column is-10 is-offset-1">
        <div class="box">
          <div>
            <h1>
              {{ $t('eventTitle') }}
              <a class="button is-primary is-medium is-pulled-right" @click="showAddModal()">
                <span class="icon is-medium">
                  <i class="fa fa-plus"></i>
                </span>
                <span>Add New Event</span>
              </a>
            </h1>
          </div>

          <!-- Show Scheduled Event -->
          <table v-if="events.length" class="table is-hoverable is-fullwidth is-striped">
            <thead>
              <tr>
              <!--  <th>id</th>
                <th>t id</th>
                <th>s id</th> -->
                <th>{{ $t('referenceLabel') }}</th>
                <th>{{ $t('descriptionLabel') }}</th>
                <th>{{ $t('urlLabel') }}</th>
                <th>{{ $t('promptTimeLabel') }}</th>
                <th>{{ $t('startDateLabel') }}</th>
                <th>{{ $t('endDateLabel') }}</th>
                <th>{{ $t('repeatLabel') }}</th>
                <th>{{ $t('actionsLabel') }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(showevent, index) in events" :key="showevent.id">
             <!--   <td><span :title="showevent.id" >id</span></td>
                <td><span :title="showevent.tablet_users_id" >id</span></td>
                <td><span :title="showevent.scheduled_by" >id</span></td> -->
                <td>{{ showevent.reference }} </td>
                <td>{{ showevent.description }}</td>
                 <td>{{ showevent.url }}</td>
                <td>{{ showevent.prompt_time }} </td>
                <td>{{ showevent.start_date }}</td>
                <td>{{ showevent.end_date }} </td>
                <td>{{ showevent.repeat }}</td>
                <td>
                  <div class="buttons">
                  <b-button type="is-success" icon-left="edit" @click="showEditModal(index)" title="Edit" ></b-button>
                  <b-button type="is-info" icon-left="copy" @click="showCopyModal(index)" title="Duplicate"></b-button>
                  <b-button type="is-danger" icon-left="trash" @click="showDeleteModal(index)" title="Delete"></b-button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>

        </div>
      </div> <!-- end Current event list -->

      <!-- Modal to Add Event -->
      <b-modal v-model="isModalVisible" scroll="keep" :can-cancel="false">
        <div class="modal-card" style="width:auto">
          <header class="modal-card-head">
            <p v-if="isAddModal" class="modal-card-title">Schedule New Event</p>
            <p v-if="isCopyModal" class="modal-card-title">Schedule Duplicate Event</p>
            <p v-if="isEditModal" class="modal-card-title">Edit Event</p>
            <p v-if="isDeleteModal" class="modal-card-title">Delete Event</p>
            <button class="delete" aria-label="close" @click="closeModal()"></button>
          </header>
          <section v-if="isDeleteModal" class="modal-card-body">
            <p>Are you sure you want to delete this event?</p>
          </section>
          <section v-if="(isAddModal || isEditModal || isCopyModal)" class="modal-card-body">
            <!-- show errors -->
            <div v-if="errors.length > 0" class="message is-danger">
              <div class="message-body">
                <ul>
                  <li v-for="error in errors" :key="error.id">{{ error }}</li>
                </ul>
              </div>
            </div>

            <!-- show form -->
            <field-reference v-model="event" is-required />

            <field-description v-model="event" is-required />

            <field-url v-model="event" />

            <field-start-date v-model="event" is-required />

            <field-end-date v-model="event" />

            <field-prompt-time v-model="event" is-required />

            <field-repeat v-model="event" is-required />

          </section>

          <footer class="modal-card-foot">
            <button v-if="isAddModal || isCopyModal" class="button is-success" @click="addEvent">Add Event</button>
            <button v-if="isEditModal" class="button is-success" @click="updateEvent">Save Changes</button>
            <button v-if="isDeleteModal" class="button is-danger" @click="deleteEvent">Delete Event</button>
            <button class="button" @click="closeModal()">Cancel</button>
          </footer>
        </div>
       </b-modal><!-- end addEventModal -->

    </div>
  </div>
</template>

<script>

import FieldReference from '@/components/Fields/FieldReference.vue'
import FieldDescription from '@/components/Fields/FieldDescription.vue'
import FieldUrl from '@/components/Fields/FieldURL.vue'
import FieldStartDate from '@/components/Fields/FieldStartDate.vue'
import FieldEndDate from '@/components/Fields/FieldEndDate.vue'
import FieldPromptTime from '@/components/Fields/FieldPromptTime.vue'
import FieldRepeat from '@/components/Fields/FieldRepeat.vue'

export default {

  components: {
    FieldReference,
    FieldDescription,
    FieldUrl,
    FieldStartDate,
    FieldEndDate,
    FieldPromptTime,
    FieldRepeat
  },

  data () {
    return {
      events: [],
      event: {},
      errors: [],
      isModalVisible: false,
      isAddModal: false,
      isEditModal: false,
      isCopyModal: false,
      isDeleteModal: false
    }
  },

  mounted () {
    this.getEvents()
  },

  methods: {

    closeModal () {
      this.event.id = null
      this.event.reference = ''
      this.event.description = ''
      this.event.url = ''
      this.event.start_date = null
      this.event.end_date = null
      this.event.prompt_time = null
      this.event.repeat = ''
      this.isModalVisible = false
      this.isAddModal = false
      this.isEditModal = false
      this.isCopyModal = false
      this.isDeleteModal = false
    },

    showAddModal () {
      this.event.start_date = new Date()
      this.isAddModal = true
      this.isModalVisible = true
    },

    showEditModal (index) {
      this.event.id = this.events[index].id
      this.event.reference = this.events[index].reference
      this.event.description = this.events[index].description
      this.event.url = this.events[index].url
      this.event.repeat = this.events[index].repeat
      this.event.start_date = new Date(this.events[index].start_date)
      this.event.end_date = this.events[index].end_date ? new Date(this.events[index].end_date) : null
      this.event.prompt_time = new Date('1970-01-01T' + this.events[index].prompt_time)
      this.isEditModal = true
      this.isModalVisible = true
    },

    showCopyModal (index) {
      this.event.reference = this.events[index].reference + ' (copy)'
      this.event.description = this.events[index].description
      this.event.url = this.events[index].url
      this.event.repeat = this.events[index].repeat
      this.event.start_date = new Date(this.events[index].start_date)
      this.event.end_date = this.events[index].end_date ? new Date(this.events[index].end_date) : null
      this.event.prompt_time = new Date('1970-01-01T' + this.events[index].prompt_time)
      this.isCopyModal = true
      this.isModalVisible = true
    },

    showDeleteModal (index) {
      this.event.id = this.events[index].id
      this.isDeleteModal = true
      this.isModalVisible = true
    },

    // Get Scheduled Events
    getEvents () {
      this.$http.get('event_schedules')
        .then(response => {
          this.events = response.data
        })
    },

    addEvent () {
      const myevent = this.event
      myevent.prompt_time = myevent.prompt_time.toLocaleString()
      console.log(myevent.prompt_time)
      this.$http.post('event_schedules', myevent)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getEvents()
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    updateEvent () {
      const myevent = this.event
      myevent.prompt_time = myevent.prompt_time.toLocaleString()
      this.$http.put('event_schedules/' + myevent.id, myevent)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getEvents()
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    deleteEvent () {
      const myeventid = this.event.id
      this.$http.delete('event_schedules/' + myeventid)
        .then(response => {
          console.log(response.data)
          this.closeModal()
          this.getEvents()
        })
        .catch(function (error) {
          console.log(error)
        })
    }

  } // end methods

}
</script>
